.navbar {
  display: flex;
  position: fixed;
  top: 0;
  padding: 20px;
  width: 90%;
  height: 30px;
  z-index: 1;
  justify-content: space-between;
  transition-timing-function: ease-in;
  transition: all 0.5s;
}
.nav__black {
  background-color: #111;
}
.nav__logo {
  object-fit: contain;
  width: 30px;
  position: fixed;
  left: 20px;
}
.nav__avatar {
  width: 30px;
  object-fit: contain;
  position: fixed;
  right: 40px;
}
